@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-SemiBold.woff2") format("woff2"), url("../fonts/AlumniSans-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-ExtraBold.woff2") format("woff2"), url("../fonts/AlumniSans-ExtraBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-Bold.woff2") format("woff2"), url("../fonts/AlumniSans-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  margin: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  min-width: 320px;
  color: #102654;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 24px;
  background-color: #ffffff;
}

a {
  color: #102654;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.button {
  padding: 24px;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  color: #ffffff;
  background-color: transparent;
  border: 4px solid #ffffff;
  cursor: pointer;
  text-align: center;
  box-sizing: border-box;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.video {
  position: absolute;
  width: 30.5%;
  height: 0;
  padding-bottom: 20.25%;
  background-color: #000000;
}
.video:hover .video__button {
  background-image: none;
}
.video--enabled {
  cursor: pointer;
}

.video__link {
  background-color: rgba(20, 54, 122, 0.5);
  z-index: 2;
}

.video__media {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

.video__button {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 72px;
  height: 72px;
  transform: translate(-50%, -50%);
  background-image: linear-gradient(44.89deg, #003D85 25.38%, #B80027 74.89%);
  border-radius: 50%;
  border: none;
  padding: 0;
  outline: none;
  display: none;
  cursor: pointer;
}
.video__button::before {
  content: "";
  background-image: url("../img/svg/arrow-video.svg");
  background-repeat: no-repeat;
  width: 17px;
  height: 17px;
  z-index: 1;
  position: absolute;
  top: 40%;
  left: 45%;
}
.video__button:focus {
  background-image: none;
  background-color: aqua;
}

.video--enabled .video__button {
  display: block;
}

.social__link {
  display: inline-block;
  padding: 15px;
}

svg {
  fill: #030a1a;
}

.main-page {
  flex-grow: 1;
}

.header {
  position: absolute;
  top: 0;
  z-index: 10;
  width: 100%;
  background-color: transparent;
  min-width: 320px;
}

.header__nav {
  padding: 30px 103px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (max-width: 1199px) {
  .header__nav {
    padding: 40px 45px 0;
  }
}
@media (max-width: 767px) {
  .header__nav {
    padding: 15px;
  }
}

.header__logo p {
  margin: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 36px;
  line-height: 43px;
  color: #ffffff;
  letter-spacing: 0.02em;
}
@media (max-width: 1199px) {
  .header__logo p {
    font-size: 24px;
    line-height: 29px;
    font-weight: 700;
  }
}
@media (max-width: 767px) {
  .header__logo p {
    font-size: 20px;
    line-height: 24px;
  }
}

.header__tel {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  color: #ffffff;
  letter-spacing: 0.03em;
}
@media (max-width: 1199px) {
  .header__tel {
    font-size: 24px;
    line-height: 29px;
    font-weight: 700;
  }
}
@media (max-width: 767px) {
  .header__tel {
    max-width: 130px;
    font-size: 20px;
    line-height: 24px;
  }
}

.block-gym__container {
  padding: 120px 103px 235px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  gap: 85px;
}

.block-gym__info p {
  max-width: 380px;
}

.intro {
  background-image: conic-gradient(from 130.31deg at 16.03% 39.06%, #B80027 -46.69deg, #003D85 32.24deg, #B80027 313.31deg, #003D85 392.24deg);
}

.intro__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 64px;
  padding: 62px 103px 51px;
}
@media (max-width: 1199px) {
  .intro__container {
    padding: 82px 45px 51px;
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .intro__container {
    padding: 115px 15px 80px;
  }
}

.intro__wrapper {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: repeat(3, auto);
  max-width: 377px;
  align-items: center;
}
@media (max-width: 1199px) {
  .intro__wrapper {
    max-width: 324px;
  }
}
@media (max-width: 767px) {
  .intro__wrapper {
    max-width: none;
  }
}

.intro__wrapper h1 {
  margin: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  color: #ffffff;
  letter-spacing: 0.05em;
  max-width: 298px;
  grid-column: 2/3;
  grid-row: 2/3;
  text-align: right;
}
@media (max-width: 1199px) {
  .intro__wrapper h1 {
    font-size: 72px;
    line-height: 86px;
    max-width: 235px;
  }
}

.intro__wrapper p {
  margin: 0;
  font-size: 14px;
  line-height: 21px;
  color: #ffffff;
  grid-column: 1/2;
  grid-row: 2/3;
  text-align: right;
}
@media (max-width: 1199px) {
  .intro__wrapper p {
    width: 113px;
  }
}

.intro__wrapper span {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 48px;
  line-height: 58px;
  color: #ffffff;
  letter-spacing: 0.03em;
  align-self: flex-end;
  grid-column: 2/3;
  grid-row: 1/2;
  padding-left: 100px;
}
@media (max-width: 1199px) {
  .intro__wrapper span {
    font-size: 34px;
    line-height: 41px;
    padding: 0;
    text-align: right;
  }
}
@media (max-width: 767px) {
  .intro__wrapper span {
    font-size: 28px;
    line-height: 34px;
  }
}

.intro__wrapper a {
  grid-column: 2/3;
  grid-row: 3/4;
  width: 230px;
  justify-self: end;
}
@media (max-width: 1199px) {
  .intro__wrapper a {
    width: 183px;
  }
}
@media (max-width: 767px) {
  .intro__wrapper a {
    width: 100%;
    grid-column: 1/3;
  }
}

@media (max-width: 1199px) {
  .intro__img {
    width: 324px;
    height: 390px;
  }
}
@media (max-width: 767px) {
  .intro__img {
    display: none;
  }
}

